import React, { useCallback } from "react";
import { useDocumentTitle } from "../../util";
import { useKanbans, useReorderKanban } from "../../util/kanban";
import {
  useKanbanSearchParams,
  useKanbansQueryKey,
  useProjectInUrl,
  useTasksQueryKey,
  useTasksSearchParams,
} from "./utils";
import KanbanColumn from "./kanban-column";
import styled from "@emotion/styled";
import SearchPanel from "./search-panel";
import { ScreentContainer } from "../../components/lib";
import { useReorderTask, useTasks } from "../../util/task";
import { Spin } from "antd";
import CreateKanban from "./create-kanban";
import TaskModal from "./task-modal";
import { DragDropContext, DropResult } from "react-beautiful-dnd";
import { Drag, Drop, DropChild } from "../../components/drag-and-drop";

const KanbanScreen: React.FC = () => {
  useDocumentTitle("看板列表");
  const { data: currentProject } = useProjectInUrl();
  const { data: kanbans, isLoading: kanbanIsLoading } = useKanbans(
    useKanbanSearchParams()
  );
  // useTasksSearchParams() 返回请求参数 传递给 useTasks 去发送请求，让react-query缓存结果
  const { isLoading: taskIsLoading } = useTasks(useTasksSearchParams());
  /* 只要 在请求看板信息 或者 任务信息 都会显示 loading*/
  const isLoading = taskIsLoading || kanbanIsLoading;

  const onDragEnd = useDragEnd();
  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <ScreentContainer>
        <h1>{currentProject?.name}看板</h1>
        <SearchPanel />
        {isLoading ? (
          <Spin size={"large"} />
        ) : (
          <ColumnsContainer>
            <Drop
              type={"COLUMN"}
              droppableId={"kanban"}
              direction={"horizontal"}
            >
              <DropChild style={{ display: "flex" }}>
                {kanbans?.map((kanban, index) => (
                  <Drag
                    key={kanban.id}
                    draggableId={`kanban-${kanban.id}`}
                    index={index} // 对应每个看板的位置索引
                  >
                    {/* 组件需要对ref进行转发 */}
                    <KanbanColumn kanban={kanban} />
                  </Drag>
                ))}
              </DropChild>
            </Drop>
            <CreateKanban />
          </ColumnsContainer>
        )}
        <TaskModal />
      </ScreentContainer>
    </DragDropContext>
  );
};

export const useDragEnd = () => {
  const { data: kanbans } = useKanbans(useKanbanSearchParams());
  const { mutate: reorderKanban } = useReorderKanban(useKanbansQueryKey());
  const { mutate: reorderTask } = useReorderTask(useTasksQueryKey());
  const { data: allTasks = [] } = useTasks(useTasksSearchParams());
  return useCallback(
    ({ source, destination, type }: DropResult) => {
      // 1. 如果没有 排序的话，什么都不做
      if (!destination) return;
      //  列排序
      if (type === "COLUMN") {
        console.log(source);
        console.log(destination);
        /*
         *   source代表拖拽起来的看板，其中的index属性是对应的看板在数组中的下标位置
         * */
        const fromId = kanbans?.[source.index].id; // 提起来的看板ID
        const toId = kanbans?.[destination.index].id; // 放下去的看板ID位置
        // 如果 没有 fromId 或者 没有 toId 或者 fromId === toId 时说明没有改变顺序
        if (!fromId || !toId || fromId === toId) return;
        /*
         *   如果 目的地ID 大于 源ID 则说明向后移动
         *   如果 目的地ID 小于 源ID 则说明向前移动
         * */
        const type = destination.index > source.index ? "after" : "before";
        reorderKanban({ fromId, referenceId: toId, type });
      }

      // 行排序
      if (type === "ROW") {
        console.log(source);
        console.log(destination);
        const fromKanbanId = +source.droppableId; // 源看板ID
        const toKanbanId = +destination.droppableId; // 目标看板ID
        const fromTask = allTasks.filter(
          (task) => task.kanbanId === fromKanbanId
        )[source.index];
        const toTask = allTasks.filter((task) => task.kanbanId === toKanbanId)[
          destination.index
        ];
        // 如果 源任务 与 目的任务 ID 相等 则退出
        if (fromTask?.id === toTask?.id) return;
        reorderTask({
          fromId: fromTask?.id,
          referenceId: toTask?.id,
          fromKanbanId,
          toKanbanId,
          type: destination.index > source.index ? "after" : "before",
        });
      }
    },
    [kanbans, reorderKanban, allTasks, reorderTask]
  );
};

export const ColumnsContainer = styled.div`
  flex: 1;
  display: flex;
  overflow-x: auto;
`;

export default KanbanScreen;
