import React, { useContext } from "react";
import * as auth from "auth-provider";
import { http } from "../util/http";
import { useMounted } from "../util";
import { useAsync } from "../util/use-async";
import { FullPageErrorFallback, FullPageLoading } from "../components/lib";
import { useQueryClient } from "react-query";
import { User } from "../types/user";

interface AuthForm {
  username: string;
  password: string;
}

interface AuthProviderType {
  user: User | null;
  login: (form: AuthForm) => Promise<unknown>;
  register: (form: AuthForm) => Promise<unknown>;
  logout: () => void;
}

// 初始化User
const bootstrapUser = async () => {
  let user = null;
  const token = auth.getToken();
  if (token) {
    const data = await http("me", { token });
    user = data.user;
  }
  return user;
};

const AuthContext = React.createContext<AuthProviderType | undefined>(
  undefined
);
AuthContext.displayName = "AuthContext"; // 在React DevTools中显示的名字

export const AuthProvider: React.FC = ({ children }) => {
  const {
    data: user,
    error,
    isLoading,
    isIdle,
    isError,
    run,
    setDate: setUser,
  } = useAsync<User | null>();
  const queryClient = useQueryClient();
  // 登录，消参
  /*
   *   回调函数（消参）
   *    (user) => setUser(user) 等价于 setUser
   *
   * */
  const login = (form: AuthForm) => auth.login(form).then(setUser);
  // 注册
  const register = (form: AuthForm) => auth.register(form).then(setUser);
  // 退出登录
  const logout = () =>
    auth.logout().then(() => {
      setUser(null);
      queryClient.clear();
    });
  useMounted(() => {
    run(bootstrapUser());
  });

  /* 登陆中 */
  if (isIdle || isLoading) {
    return <FullPageLoading />;
  }

  /* 出错了 */
  if (isError) {
    return <FullPageErrorFallback error={error} />;
  }

  return (
    <AuthContext.Provider value={{ user, login, register, logout }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error("useAuth必须在AuthProvider中使用");
  }
  return context;
};
