import { useSetUrlSearchParam, useUrlQueryParam } from "../../util/url";
import { useCallback, useMemo } from "react";
import { useProjectInfo } from "../../util/project";
export const useProjectsSearchParams = () => {
  const [param, setParam] = useUrlQueryParam(["name", "personId"]);
  /*
   *  每次返回一个新对象，导致无限循环
   * */
  return [
    useMemo(
      () => ({
        ...param,
        personId: Number(param.personId) ?? undefined,
      }),
      [param]
    ),
    setParam,
  ] as const;
};
export const useProjectsQueryKey = () => {
  const [params] = useProjectsSearchParams();
  return ["projects", params];
};

// 全局状态管理器
export const useProjectModal = () => {
  const [{ projectCreate }, setProjectCreate] = useUrlQueryParam([
    "projectCreate",
  ]);

  const [{ editingProjectId }, setEditingProjectId] = useUrlQueryParam([
    "editingProjectId",
  ]);

  const setUrlParams = useSetUrlSearchParam();

  const { data: editingProject, isLoading } = useProjectInfo(
    Number(editingProjectId)
  );

  const open = useCallback(
    () => setProjectCreate({ projectCreate: true }),
    [setProjectCreate]
  );

  const close = useCallback(() => {
    setUrlParams({ projectCreate: "", editingProjectId: "" });
  }, [setUrlParams]);

  const startEdit = useCallback(
    (id: number) => setEditingProjectId({ editingProjectId: id }),
    [setEditingProjectId]
  );

  return {
    projectModalOpen: projectCreate === "true" || Boolean(editingProjectId),
    open,
    close,
    editingProject, // 项目详情信息
    isLoading,
    startEdit,
  };
};
