import { useLocation } from "react-router-dom";
import { useProjectInfo } from "../../util/project";
import { useUrlQueryParam } from "../../util/url";
import { useCallback, useMemo } from "react";
import { useTask } from "../../util/task";
import { useDebounce } from "../../util";

/* 从URL中获取 当前项目ID */
export const useProjectIdInUrl = () => {
  /*const { projectId } = useParams();
  return projectId;*/
  const { pathname } = useLocation();
  const id = pathname.match(/projects\/(\d+)/)?.[1]; // ()包裹的字符集 匹配中了也会返回到数组中
  return Number(id);
};

// 根据项目ID获取 【项目详情】
export const useProjectInUrl = () => useProjectInfo(useProjectIdInUrl());

// 返回 项目ID 用于 react-query 发送请求
export const useKanbanSearchParams = () => ({ projectId: useProjectIdInUrl() });

//  返回 看板的 queryKey 值
export const useKanbansQueryKey = () => ["kanbans", useKanbanSearchParams()];

// 返回 项目ID 用于 react-query 发送请求
export const useTasksSearchParams = () => {
  const [params] = useUrlQueryParam(["name", "typeId", "processorId", "tagId"]);
  const projectId = useProjectIdInUrl();
  const debouncedName = useDebounce(params.name, 200);
  return useMemo(
    () => ({
      projectId,
      typeId: Number(params.typeId) || undefined,
      processorId: Number(params.processorId) || undefined,
      tagId: Number(params.tagId) || undefined,
      name: debouncedName,
    }),
    [projectId, params, debouncedName]
  );
};

// 返回 任务的 queryKey 值
export const useTasksQueryKey = () => ["tasks", useTasksSearchParams()];

// 控制 taskModal 状态的 Hook
export const useTasksModal = () => {
  const [{ editingTaskId }, setEditingTaskId] = useUrlQueryParam([
    "editingTaskId",
  ]);
  // 拿着 任务Id 去发请求 获取任务详情
  const { data: editingTask, isLoading } = useTask(Number(editingTaskId));
  const startEdit = useCallback(
    (id: number) => {
      setEditingTaskId({ editingTaskId: id });
    },
    [setEditingTaskId]
  );

  const close = useCallback(() => {
    setEditingTaskId({ editingTaskId: "" });
  }, [setEditingTaskId]);

  return {
    editingTaskId,
    editingTask,
    startEdit,
    close,
    isLoading,
  };
};
