import React, { useEffect } from "react";
import { useForm } from "antd/es/form/Form";
import { useTasksModal, useTasksQueryKey } from "./utils";
import { useDeleteTask, useEditTask } from "../../util/task";
import { Modal, Form, Input, Button } from "antd";
import UserSelect from "../../components/user-select";
import TaskTypeSelect from "../../components/task-type-select";

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};

const TaskModal: React.FC = () => {
  // 通过 useForm 返回的 form 绑定 Form组件
  const [form] = useForm();
  // 当 editingTask 数据发生变化，react-query 会及时更新
  const { editingTask, editingTaskId, close } = useTasksModal(); // 获取最新的任务数据
  const { mutateAsync: editTask, isLoading: editLoading } = useEditTask(
    useTasksQueryKey()
  );
  const { mutate: deletetTask } = useDeleteTask(useTasksQueryKey());

  const onCancel = () => {
    close();
    form.resetFields(); // 重置表单
  };

  const onOK = async () => {
    // 传递参数，修改任务信息
    await editTask({ ...editingTask, ...form.getFieldsValue() });
    close();
  };

  const startEdit = () => {
    Modal.confirm({
      okText: "确定",
      cancelText: "取消",
      title: "确定删除看板吗?",
      async onOk() {
        await deletetTask({ id: Number(editingTaskId) });
        close();
      },
    });
  };

  // 当editingTask变化后，设置表单数据【乐观更新】
  useEffect(() => {
    form.setFieldsValue(editingTask);
  }, [editingTask, form]);

  return (
    <Modal
      okText={"确认"}
      cancelText={"取消"}
      confirmLoading={editLoading}
      title={"编辑任务"}
      visible={!!editingTaskId} /* 通过任务ID显示模态框 */
      onOk={onOK}
      onCancel={onCancel}
      forceRender={true}
    >
      <Form initialValues={editingTask} form={form} {...layout}>
        <Form.Item
          label={"任务名"}
          name={"name"}
          rules={[{ required: true, message: "请输入任务名" }]}
        >
          <Input placeholder={"请输入"} />
        </Form.Item>
        <Form.Item label={"经办人"} name={"processorId"}>
          <UserSelect defaultOptionName={"经办人"} />
        </Form.Item>
        <Form.Item label={"类型"} name={"typeId"}>
          <TaskTypeSelect defaultOptionName={""} />
        </Form.Item>
      </Form>
      <div style={{ textAlign: "right" }}>
        <Button onClick={startEdit} style={{ fontSize: "14px" }} size={"small"}>
          删除
        </Button>
      </div>
    </Modal>
  );
};

export default TaskModal;
