import React from "react";
import ProjectListScreen from "./screeens/project-list";
import { useAuth } from "./context/auth-context";
import styled from "@emotion/styled";
import { ButtonNoPadding, Row } from "./components/lib";
import { ReactComponent as SoftWareLogo } from "assets/software-logo.svg";
import { Button, Dropdown, Menu } from "antd";
import { useDocumentTitle, resetRoute } from "./util";
import { Routes, Route, Navigate } from "react-router-dom";

import ProjectScreen from "screeens/project";
import ProjectModal from "./screeens/project-list/project-modal";
import ProjectPopover from "./components/project-popover";
import UserPopover from "./components/user-popover";

const AuthenticatedApp: React.FC = () => {
  useDocumentTitle("项目列表", false);

  return (
    <Container>
      <PageHeader />
      <Main>
        {/* 所有路由配置用Routes包裹起来 */}
        <Routes>
          <Route path={"/projects"} element={<ProjectListScreen />} />
          <Route path={"/projects/:projectId/*"} element={<ProjectScreen />} />
          <Navigate to={`/projects`} />
        </Routes>
      </Main>
      <ProjectModal />
    </Container>
  );
};

/*
 *   1. display: grid; 本容器用【网格布局】
 *   2. grid-template-rows: 6rem 1fr 6rem; 控制每行的高度
 *   3. grid-template-columns: 20rem 1fr 20rem; 控制每列的宽度
 *   4. grid-area: xxx; 给每个子元素起名字
 *   5. grid-template-areas:  表述子元素怎么排列
 *          "header header header"  header独占一行
 *          "nav main aside"
 *          "footer footer footer"  footer独占一行
 *      ;
 *   6. grid-gap: 10rem; 每一块之间的距离
 *
 * */

/*
 *   grid 和 flex 各自的应用场景
 *   1. 要考虑，是一维布局 还是 二维布局
 *    - 一维布局用flex，二维布局用grid
 *   2. 是从内容出发还是从布局出发？
 *    - 从内容出发：有一组内容（数量不固定），希望他们均匀的分布在容器中，由内容自己的大小决定占据的空间大小
 *    - 从布局出发：先规划网格（数量固定），然后再把元素往里填充
 *    从内容出发, 用 flex
 *    从布局出发，用 grid
 * */

const PageHeader: React.FC = () => {
  return (
    <Header between={true}>
      <HeaderLeft gap={true}>
        <ButtonNoPadding type={"link"} onClick={resetRoute}>
          <SoftWareLogo width={"18rem"} color={"rgb(38, 132, 255"} />
        </ButtonNoPadding>
        <ProjectPopover />
        <UserPopover />
      </HeaderLeft>
      <HeaderRight>
        <User />
      </HeaderRight>
    </Header>
  );
};

const User: React.FC = () => {
  const { logout, user } = useAuth();
  return (
    <Dropdown
      overlay={
        <Menu>
          <Menu.Item key={"logout"}>
            <Button type={"link"} onClick={logout}>
              登出
            </Button>
          </Menu.Item>
        </Menu>
      }
    >
      <Button type={"link"}>Hi, {user?.name}</Button>
    </Dropdown>
  );
};

const Container = styled.div`
  display: grid;
  grid-template-rows: 6rem 1fr;
  height: 100vh;
`;

const Header = styled(Row)`
  padding: 0 3.2rem;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
  z-index: 1;
`;

const HeaderLeft = styled(Row)``;

const HeaderRight = styled.div``;
const Main = styled.main`
  display: flex;
  overflow: hidden;
`;

export default AuthenticatedApp;
