import React, { useCallback, useEffect } from "react";
import { Drawer, Button, Spin, Form, Input } from "antd";
import { useProjectModal, useProjectsQueryKey } from "./util";
import UserSelect from "../../components/user-select";
import { useAddProject, useEditProject } from "../../util/project";
import { useForm } from "antd/es/form/Form";
import { ErrorBox } from "../../components/lib";
import { useUrlQueryParam } from "../../util/url";
import styled from "@emotion/styled";

const ProjectModal: React.FC = () => {
  const { projectModalOpen, close, editingProject, isLoading } =
    useProjectModal();
  const useMutateProject = editingProject ? useEditProject : useAddProject;

  const {
    mutateAsync,
    error,
    isLoading: mutateLoading,
  } = useMutateProject(useProjectsQueryKey());
  const [form] = useForm();
  const [{ personId }] = useUrlQueryParam(["personId"]);
  const onFinish = useCallback(
    (formValues: any) => {
      console.log(formValues);
      // 调用 mutate / mutateAsync 去执行 请求函数
      mutateAsync({ ...editingProject, ...formValues, personId }).then(() => {
        // 清空表单数据，关闭Modal
        form.resetFields();
        close();
      });
    },
    [mutateAsync, editingProject, form, close, personId]
  );

  const closeModal = useCallback(() => {
    form.resetFields();
    close();
  }, [form, close]);

  const title = editingProject ? "编辑项目" : "创建项目";

  useEffect(() => {
    form.setFieldsValue(editingProject);
  }, [form, editingProject]);

  return (
    <Drawer
      /* forceRender表示预渲染 Drawer 内元素 */
      forceRender={true}
      destroyOnClose={true}
      onClose={closeModal}
      visible={projectModalOpen}
      width={"100%"}
    >
      <Container>
        {isLoading ? (
          <Spin size={"large"} />
        ) : (
          <>
            <h1>{title}</h1>
            <ErrorBox error={error} />
            <Form
              form={form}
              layout={"vertical"}
              style={{ width: "40rem" }}
              onFinish={onFinish}
            >
              <Form.Item
                label={"名称"}
                name={"name"}
                rules={[{ required: true, message: "请输入项目名" }]}
              >
                <Input placeholder={"请输入项目名称"} />
              </Form.Item>
              <Form.Item
                label={"部门"}
                name={"organization"}
                rules={[{ required: true, message: "请输入部门名" }]}
              >
                <Input placeholder={"请输入部门名称"} />
              </Form.Item>
              <Form.Item label={"负责人"} name={"personId"}>
                <UserSelect defaultOptionName={"负责人"} />
              </Form.Item>
              <Form.Item style={{ textAlign: "right" }}>
                <Button
                  loading={mutateLoading}
                  type={"primary"}
                  htmlType={"submit"}
                >
                  提交
                </Button>
              </Form.Item>
            </Form>
          </>
        )}
        {/*<h1>Project Modal</h1>*/}
        <Button onClick={closeModal}>关闭</Button>
      </Container>
    </Drawer>
  );
};

const Container = styled.div`
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

export default ProjectModal;
