// 定义一个组件，需要让别人使用，但是他们需要知道需要传入什么参数
import { Dropdown, Menu, Modal, Table, TableProps } from "antd";
import dayjs from "dayjs";
import { Link } from "react-router-dom";
import Pin from "../../components/pin";
import { useDeleteProject, useEditProject } from "../../util/project";
import { ButtonNoPadding } from "../../components/lib";
import { useProjectModal, useProjectsQueryKey } from "./util";
import { Project } from "../../types/project";
import { User } from "../../types/user";

interface ListProps extends TableProps<Project> {
  users: User[];
}

// props 的类型等价于 type PropsType = Omit<ListProps, "users">;
const List = ({ users, ...props }: ListProps) => {
  const { mutate } = useEditProject(useProjectsQueryKey());

  // 点击后 重新调用run函数，获取到数据后刷新列表
  const pinProject = (id: number) => (pin: boolean) => mutate({ id, pin });

  return (
    <Table
      pagination={false}
      columns={[
        {
          title: <Pin checked={true} disabled={true} />,
          render(value, project) {
            return (
              <Pin
                checked={project.pin}
                onCheckedChange={pinProject(project.id)}
              />
            );
          },
        },
        {
          key: "name",
          title: "名称",
          // dataIndex: "name",
          sorter(a, b) {
            // 字符比较（-1升序，1降序，0不变）
            return a.name.localeCompare(b.name);
          },
          render(_, project) {
            // to属性必须传入string类型，传入number类型不会报错，但是功能失效，无法跳转
            return <Link to={`${project.id}`}>{project.name}</Link>;
          },
        },
        {
          key: "organization",
          title: "部门",
          dataIndex: "organization",
        },
        {
          key: "created",
          title: "创建时间",
          render(_, project) {
            return <span>{dayjs(project.created).format("YYYY-MM-DD")}</span>;
          },
        },
        {
          key: "responsible",
          title: "负责人",
          render(_, project) {
            return (
              <span>
                {users.find((user) => user.id === project.personId)?.name ||
                  "未知"}
              </span>
            );
          },
        },
        {
          render(_, project) {
            return <More project={project} />;
          },
        },
      ]}
      rowKey={"name"}
      {...props}
    />
  );
};

const More = ({ project }: { project: Project }) => {
  const { startEdit } = useProjectModal();
  const editProject = (id: number) => () => startEdit(id);
  const { mutate: deleteProject } = useDeleteProject(useProjectsQueryKey());
  const confirmDeleteProject = (id: number) => {
    Modal.confirm({
      title: "确定删除这个项目吗？",
      content: "Some descriptions",
      okText: "确定",
      cancelText: "取消",
      onOk() {
        deleteProject({ id });
      },
      onCancel() {},
    });
  };
  return (
    <Dropdown
      overlay={
        <Menu>
          <Menu.Item key={"edit"} onClick={editProject(project.id)}>
            编辑
          </Menu.Item>
          <Menu.Item
            key={"delete"}
            onClick={() => confirmDeleteProject(project.id)}
          >
            删除
          </Menu.Item>
        </Menu>
      }
    >
      <ButtonNoPadding type={"link"}>...</ButtonNoPadding>
    </Dropdown>
  );
};

export default List;
