// 在一个函数里，改变传入的对象本身是不好的
/*
 *   清除空值： 0  NaN  ''  false  null  undefined
 *   0 是有效值
 * */
export const cleanObject = (object: { [key: string]: unknown }) => {
  //  Object.assign({}, object);
  const result = { ...object }; // 不能从unknown读属性
  Object.keys(result).forEach((key) => {
    // 0是有效值，不应该被删除
    const value = result[key]; // 不能从空对象上读属性
    if (isFalsy(value)) {
      delete result[key];
    }
  });
  return result;
};

/*
 * 返回组件的挂载状态，如果还没挂载或者已经卸载，返回false：反之，返回true
 * */
export const useMountedRef = () => {
  const mountedRef = useRef(false); // 还没有挂载时返回false
  useEffect(() => {
    mountedRef.current = true; // 挂载后返回true
    return () => {
      mountedRef.current = false; // 卸载后返回false
    };
  }, []);

  return mountedRef;
};

// 只在加载完成时执行的函数
/*
 *  hook 只能在hook中【use开头】，函数组件中运行
 * */
export const useMounted = (callback: () => void) => {
  useEffect(() => {
    callback();
    //  TODO: 依赖项里加上callback会造成无线循环，这个和useCallback以及useMemo有关系
    //  eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
};

