/*
 *   debounce 原理：
 *   0s ------> 1s ------> 2s ------> ...
 *     三个log函数都是同步操作，所以它们都是在 0 - 1s 这个时间段内瞬间完成的。
 *     log()#1 // timeout#1
 *     log()#2 // 发现 timeout#1 取消之，然后设置 timeout#2
 *     log()#3 // 发现 timeout#2 取消之，然后设置 timeout#3
 *             // 所以, log()#3 结束后，就只剩 timeout#3 在独自等待了
 * */

// 用泛型来规范类型
export const useDebounce = <V>(value: V, delay?: number) => {
  // 定义一个新的状态，更新频率与value不一致
  const [debounceValue, setDebounceValue] = useState(value);
  useEffect(() => {
    // 每次在value变化之后，设置一个定时器
    const timeout = setTimeout(() => setDebounceValue(value), delay);
    // 在执行【当前 effect 之前】对【上一个 effect】进行【清除】
    // 【清除上一个 effect 】中设置的定时器
    return () => clearTimeout(timeout);
  }, [value, delay]);

  return debounceValue;
};

// 自动识别传进来参数的类型 ==》 泛型
/*
 *   遇到的问题：
 *     1. 如何从泛型身上【获取】属性/方法
 *     2. 如何【修改】泛型的值
 * */
export const useArray = <A>(initialArray: A[]) => {
  const [value, setValue] = useState(initialArray);
  const add = (item: A) => setValue([...value, item]);
  const removeIndex = (idx: number) => {
    const newValue = [...value];
    newValue.splice(idx, 1);
    setValue(newValue);
  };
  const clear = () => setValue([]);
  return { value, add, removeIndex, clear };
};

export const useDocumentTitle = (title: string, keepOnUnmount = true) => {
  const oldTitle = useRef(document.title).current;
  // 页面加载时：oldTitle === 旧title 'React App'
  // 页面加载后：oldTitle === 新title

  useEffect(() => {
    document.title = title;
  }, [title]);

  useEffect(
    () => {
      return () => {
        // 如果为 false 就赋值为原来的title
        if (!keepOnUnmount) {
          // 不指定依赖，读到的就是旧title
          document.title = oldTitle;
        }
      };
    },
    // eslint-disable-next-line  react-hooks/exhaustive-deps
    [keepOnUnmount, oldTitle]
  );
};

/*
 *  href 是当前路径地址
 *  origin 是【根地址】
 * */
export const resetRoute = () => (window.location.href = window.location.origin);
