import { useHttp } from "./http";
import { QueryKey, useQuery, useMutation } from "react-query";
import { cleanObject } from "./index";
import { Kanban } from "../types/kanban";
import {
  useAddConfig,
  useDeleteConfig,
  useReorderKanbanConfig,
} from "./use-optimistic-options";

export const useKanbans = (param?: Partial<Kanban>) => {
  const client = useHttp();

  return useQuery<Kanban[]>(["kanbans", param], () =>
    client("kanbans", { data: cleanObject(param ?? {}) })
  );
};

export const useAddKanban = (queryKey: QueryKey) => {
  const client = useHttp();

  return useMutation(
    (params: Partial<Kanban>) =>
      client(`kanbans`, { data: params, method: "POST" }),
    useAddConfig(queryKey)
  );
};

export const useDeleteKanban = (queryKey: QueryKey) => {
  const client = useHttp();

  return useMutation(
    ({ id }: { id: number }) => client(`kanbans/${id}`, { method: "DELETE" }),
    useDeleteConfig(queryKey)
  );
};

/* # 1. 定义排序参数 */
export interface SortProps {
  fromId: number; // 要重新排序的Item
  referenceId: number; // 目标 Item
  type: "before" | "after"; // 放在目标 Item 的 前还是后
  fromKanbanId?: number; // 对应任务来说的：来自的看板ID
  toKanbanId?: number; // 对应任务来说的：目的地看板ID
}
/* # 2. 发送请求 */
export const useReorderKanban = (queryKey: QueryKey) => {
  const client = useHttp();
  return useMutation((params: SortProps) => {
    return client("kanbans/reorder", {
      data: params,
      method: "POST",
    });
  }, useReorderKanbanConfig(queryKey));
};
