import React from "react";
import List from "./list";
import SearchPanel from "./search-panel";
import { useDebounce } from "../../util";
import styled from "@emotion/styled";
import { useProject } from "../../util/project";
import { useUsers } from "../../util/user";
import { Helmet } from "react-helmet";
import { useProjectModal, useProjectsSearchParams } from "./util";
import { ButtonNoPadding, ErrorBox, Row } from "../../components/lib";
// import Test from "./test-closure";

/*
 * 自动切换变量
 * npm start 使用 .env.development中的变量
 * npm run build 使用 .env中的变量
 * */
const ProjectListScreen: React.FC = () => {
  /*
   *   每次返回一个新的{name: xx, personId: xxx}造成的重新渲染
   * */
  // const [keys] = useState<("name" | "personId")[]>(["name", "personId"]);
  const [param, setParam] = useProjectsSearchParams();
  const {
    isLoading,
    error,
    data: list,
    // retry,
  } = useProject(useDebounce(param, 500));
  const { data: users } = useUsers();
  const { open } = useProjectModal();
  return (
    <Container>
      <Helmet>
        <title>项目列表</title>
      </Helmet>
      <Row between={true}>
        <h1>项目列表</h1>
        <ButtonNoPadding onClick={open} type={"link"}>
          创建项目
        </ButtonNoPadding>
      </Row>
      <SearchPanel users={users ?? []} param={param} setParam={setParam} />
      <ErrorBox error={error} />
      <List
        // refresh={retry}
        users={users ?? []}
        loading={isLoading}
        dataSource={list ?? []}
      />
    </Container>
  );
};

ProjectListScreen.whyDidYouRender = false;

const Container = styled.div`
  padding: 3.2rem;
  flex: 1;
`;

export default ProjectListScreen;
