import React from "react";

interface IProps {
  name: string;
  keyword: string;
}

const Mark: React.FC<IProps> = ({ name, keyword }) => {
  const arr = name.split(keyword);
  if (!keyword) {
    return <>{name}</>;
  }
  return (
    <>
      {arr.map((str, index) => (
        <span key={index}>
          {str}
          {/*只要不是最后一个就加上关键字 */}
          {index === arr.length - 1 ? null : (
            <span style={{ color: "#257afd" }}>{keyword}</span>
          )}
        </span>
      ))}
    </>
  );
};

export default Mark;
