import React, { Component } from "react";

/*
 * children
 * fallbackRender【备用方案】
 *
 * */
type FallbackRender = (props: { error: Error | null }) => React.ReactElement;
interface IProps {
  fallbackRender: FallbackRender; // 一个函数组件
}
interface IState {
  error: Error | null;
}
export class ErrorBoundary extends Component<
  React.PropsWithChildren<IProps>,
  IState
> {
  state = { error: null };
  /*
   * 子组件发生错误后，会调用该方法，并且接收子组件发生的错误, 同时返回一个state对象，通过state来渲染错误组件
   * */
  static getDerivedStateFromError(error: Error) {
    /*
     * */
    return { error };
  }

  render() {
    const { error } = this.state;
    const { fallbackRender, children } = this.props;
    if (error) return fallbackRender({ error });
    return children;
  }
}

/*function App1(): React.ReactElement {
  return <div>React.ReactElement</div>;
}

function App2(): React.ReactNode {
  return <div>React.ReactNode</div>;
}

function App3(): JSX.Element {
  return <div>xxx</div>;
}*/

export default ErrorBoundary;
