import React from "react";
import { Button, Card, Dropdown, Menu, Modal } from "antd";
import { Kanban } from "../../types/kanban";
import { useTasks } from "../../util/task";
import {
  useKanbansQueryKey,
  useTasksModal,
  useTasksSearchParams,
} from "./utils";
import { useTaskTypes } from "../../util/task-type";
import bugIcon from "assets/bug.svg";
import taskIcon from "assets/task.svg";
import styled from "@emotion/styled";
import CreateTask from "./create-task";
import { Task } from "../../types/task";
import Mark from "../../components/mark";
import { useDeleteKanban } from "../../util/kanban";
import { Row } from "components/lib";
import { Drag, Drop, DropChild } from "../../components/drag-and-drop";

interface IProps {
  kanban: Kanban;
}
interface TaskTypeIconIProps {
  id: number;
}
const TaskTypeIcon: React.FC<TaskTypeIconIProps> = ({ id }) => {
  const { data: taskTypes } = useTaskTypes();
  const name = taskTypes?.find((taskType) => taskType.id === id)?.name;
  if (!name) return null;
  return <img alt={"图标"} src={name === "task" ? taskIcon : bugIcon} />;
};

const KanbanColumn = React.forwardRef<HTMLDivElement, IProps>(
  ({ kanban, ...props }, ref) => {
    const { data: allTasks } = useTasks(useTasksSearchParams());
    const tasks = allTasks?.filter((task) => task.kanbanId === kanban.id);
    return (
      <Container ref={ref} {...props}>
        <Row between={true}>
          <h3>{kanban.name}</h3>
          <More kanban={kanban} />
        </Row>
        <TaskContainer>
          <Drop
            droppableId={`${kanban.id}`}
            type={"ROW"}
            direction={"vertical"}
          >
            <DropChild style={{ minHeight: "5px" }}>
              {tasks?.map((task, taskIndex) => (
                <Drag
                  key={taskIndex}
                  draggableId={`${task.id}`}
                  index={taskIndex}
                >
                  <div>
                    <TaskCard key={task.id} task={task} />
                  </div>
                </Drag>
              ))}
            </DropChild>
          </Drop>
          <CreateTask kanbanId={kanban.id} />
        </TaskContainer>
      </Container>
    );
  }
);

const More: React.FC<IProps> = ({ kanban }) => {
  const { mutateAsync } = useDeleteKanban(useKanbansQueryKey());
  const startEdit = () => {
    Modal.confirm({
      okText: "确定",
      cancelText: "取消",
      title: "确定删除看板吗?",
      onOk() {
        return mutateAsync({ id: kanban.id });
      },
    });
  };
  const overlay = (
    <Menu>
      <Menu.Item key={"delete"}>
        <Button type={"link"} onClick={startEdit}>
          删除
        </Button>
      </Menu.Item>
    </Menu>
  );
  return (
    <Dropdown overlay={overlay}>
      <Button type={"link"}>......</Button>
    </Dropdown>
  );
};

const TaskCard: React.FC<{ task: Task }> = ({ task }) => {
  const { startEdit } = useTasksModal();
  const { name: keyword } = useTasksSearchParams();
  return (
    <Card
      style={{ marginBottom: "0.5rem", cursor: "pointer" }}
      onClick={() => startEdit(task.id)}
      key={task.id}
    >
      <Mark keyword={keyword} name={task.name} />
      <TaskTypeIcon id={task.id} />
    </Card>
  );
};

// const TaskCard = styled(Card)`
//   margin-bottom: 0.5rem;
//   cursor: pointer;
// `;

export const Container = styled.div`
  min-width: 27rem;
  border-radius: 6px;
  background-color: rgb(244, 245, 247);
  display: flex;
  flex-direction: column;
  padding: 0.7rem 0.7rem 1rem;
  margin-right: 5rem;
`;

const TaskContainer = styled.div`
  overflow: scroll;
  flex: 1;
  /* 超出容器时，不显示滚动条，依旧可是滚动 */
  ::-webkit-scrollbar {
    display: none;
  }
`;

export default KanbanColumn;
