import React, { useState } from "react";
import { Row, ScreentContainer } from "components/lib";
import { useDocumentTitle } from "../../util";
import { useProjectInUrl } from "../Kanban/utils";
import { useDeleteEpic, useEpics } from "util/epic";
import { useEpicSearchParams, useEpicsQueryKey } from "./util";
import { Button, List } from "antd";
import dayjs from "dayjs";
import { useTasks } from "../../util/task";
import { Link } from "react-router-dom";
import CreateEpic from "./create-epic";

const EpicScreen: React.FC = () => {
  useDocumentTitle("任务组列表");
  const { data: currentProject } = useProjectInUrl();
  const { data: epics } = useEpics(useEpicSearchParams());
  const { data: tasks } = useTasks({ projectId: currentProject?.id });
  const { mutate: deleteEpic } = useDeleteEpic(useEpicsQueryKey());
  const [epicCreateOpen, setEpicCreateOpen] = useState(false);
  return (
    <ScreentContainer>
      <Row between={true}>
        <h1>{currentProject?.name}任务组</h1>
        <Button
          onClick={() => {
            setEpicCreateOpen(true);
          }}
          type={"link"}
        >
          创建任务组
        </Button>
      </Row>

      <List
        style={{ overflowY: "auto" }}
        dataSource={epics}
        itemLayout={"vertical"}
        renderItem={(epic) => (
          <List.Item>
            <List.Item.Meta
              title={
                <Row between={true}>
                  <span>{epic.name}</span>
                  <Button
                    type={"link"}
                    onClick={() => deleteEpic({ id: epic.id })}
                  >
                    删除
                  </Button>
                </Row>
              }
              description={
                <div>
                  <div>开始时间：{dayjs(epic.start).format("YYYY-MM-DD")}</div>
                  <div>结束时间: {dayjs(epic.end).format("YYYY-MM-DD")}</div>
                </div>
              }
            />
            <div>
              {tasks
                ?.filter((task) => task.epicId === epic.id)
                .map((task) => (
                  <Link
                    key={task.id}
                    to={`/projects/${currentProject?.id}/kanban?editingTaskId=${task.id}`}
                  >
                    {task.name}
                  </Link>
                ))}
            </div>
            <CreateEpic
              onClose={() => setEpicCreateOpen(false)}
              visible={epicCreateOpen}
            />
          </List.Item>
        )}
      />
    </ScreentContainer>
  );
};

export default EpicScreen;
